






























var dateUtils = require('../../common/util.js').dateUtils;

export default {
	data() {
		return {
			tabbarIndex: 0,
			tabbarList: [],
			productList: [],
			reload: false
		}
	},
	onLoad(e) {
		uni.showLoading();
		this.getType();
		this.getList();
	},
	onPullDownRefresh() {
		this.reload = true;
		this.last_id = "";
		this.getList();
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		getType() {
			uni.request({
				url: this.$requestUrl + 'get_comic_type',
				method: 'GET',
				success: res => {
					let typeInfo = res.data;
					this.tabbarList = typeInfo.data;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		getList(type = '-1') {
			let data = {
				'type': type
			};
			uni.request({
				url: this.$requestUrl + 'get_comic_list',
				data: data,
				success: (data) => {
					let comicInfo = data.data;
					if (comicInfo.status == 1) {
						let list = comicInfo.data
						this.productList = this.reload ? list : this.productList.concat(list);
						this.reload = false;
					}
				},
				fail: (data, code) => {
					console.log('fail' + JSON.stringify(data));
				},
				complete: () => {
					uni.hideLoading();
				}
			})
		},
		goDetail(e) {
			let detail = {
				id: e.id,
				title: e.title,
				cover: e.cover,
				release_type_name: e.release_type_name,
				created_at: e.created_at
			}
			uni.navigateTo({
				url: "../comic-detail/comic-detail?detailData=" + JSON.stringify(detail)
			})
		},
		async chooseType(e) {
			uni.showLoading();
			let currentIndex = e.target.dataset.current;
			if (this.tabbarIndex === currentIndex) {
				return false;
			} else {
				this.tabbarIndex = currentIndex;
				this.reload = true;
				this.getList(e.target.dataset.id)
			}
		}
	},
}



